.Kids-container{
    background: rgb(16, 27, 36);
    padding: 120px 10%;
    margin-top: -80px;
}
.Kids-container .title{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
    

}
.Kids-container .title h1 {
    padding: 5px;
    text-align: center;
    width: 400px;
    color: white;
    font-size: 2rem;
    font-weight: lighter;
    border-bottom: white solid 1px;
    letter-spacing: 5px;
}
.Kids-photos{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(10, 13vw);
    /* grid-gap: 15px; */
}
.Kids-Container .single-image{
    display: none;
}
.gallery-item{
    width: 100%;
    height: 100%;
    /* object-fit: contain; */
    overflow: hidden;
    
}
.gallery-item img{
    opacity: .9;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: ease .2s;
    cursor: url("../../img/zoom-in-thick.svg"), auto;
}
.gallery-item img:hover{
    border: white solid 1px;
    transform: scale(1.05);
    opacity: 1;
    
}
