.none{
    display: none;
}
.fullscreen-image{
    display: flex;
    justify-content: center;
    align-items: center;
    background:rgb(10, 25, 32);
    width: 100vw;
    height: 100vh;
    color:  white;
}
.fullscreen-image img{
    width: auto;
    height: 85%;
    transition: ease 1s;
    transition-duration: ease .5s;
    /* border: white solid 1px; */
}
