.next {
    position: absolute;
    right: 0;
    height: 100vh;
    width: 50vw;
    background: none;
    border: none;
    color: rgb(117, 115, 115);
    font-size: 4rem;
    transition: ease .3s;
    cursor: pointer;text-align: right;
    cursor: url("../../img/right-2.svg"), auto;
}
.previous {
    position: absolute;
    left: 0;
    height: 100vh;
    width: 50vw;
    background: none;
    border: none;
    color: rgb(117, 115, 115);
    font-size: 4rem;
    transition: ease .3s;
    cursor: pointer;
    text-align: left;
    cursor: url("../../img/left-2.svg"), auto;
    }
.close {
    position: absolute;
    bottom: 10px;
    height: 50px;
    width: 80px;
    background: none;
    border: none;
    color: rgb(185, 183, 183);
    font-size: 1.2rem;
    letter-spacing: 5px;
    font-weight: 600;
    transition: ease .3s;
    cursor: pointer;
    font-family: 'Poiret One', cursive;
    text-transform: uppercase;
}
.fullscreen-image  button:hover{
    color: rgb(243, 240, 240);
    transition: ease .3s;
}
.fullscreen-image  button:focus{
    outline: none;
}
