@media screen and (max-width: 950px){
    .Kids-photos{
        display: flex;
        flex-direction: column;
        
    }
    .fullscreen-image{
        display: flex;
        justify-content: center;
        align-items: center;
        background:rgb(10, 25, 32);
        width: 100vw;
        height: 100vh;
        color:  white;
    }
    .fullscreen-image img{
        
        
        width: 100%;
        height: auto;
        
    }
    .close {
        position: absolute;
        top: 20px;
        
        height: 50px;
        width: 80px;
        background: none;
        border: none;
        color: rgb(185, 183, 183);
        font-size: 1.2rem;
        letter-spacing: 5px;
        font-weight: 600;
        transition: ease .3s;
        cursor: pointer;
        font-family: 'Poiret One', cursive;
        text-transform: uppercase;
    }
}










